<script>
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../../utils/Util'

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Sertifikat Setting',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Sertifikat Setting'+ ' ' +this.$route.params.action,
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			aksiEdit: false,
      		aksiSave: false,
			alert:false,
		}
	},
	mounted() {
		console.log("alamat", Utils.Host())
		this.Getdata()
		if (this.$route.params.action === 'tambah') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'edit') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'detail') {
		this.aksiEdit = false
		this.aksiSave = false
    }
  },
//   computed: {
//   },
  methods: {
    async Getdata() {
      try {
        const action = this.$route.params.action
        if (action === 'detail' || action === 'edit') {
          let response = await axios.get(this.api+"/sertifikatsetting/"+this.$route.params.id, Utils.authHeader());
          this.data = response.data.Data[0];
        }
      } catch (err) {
          console.error(err);
      }
    },
    async openEdit(id) {
        document.location = '/sertifikatsettingaction/edit/' + id
        // location.reload();
    },
    validation() {
      if (!this.data.f_keterangan) {
          return { code: 500, message: 'Keterangan Data ini harus diisi' }
      } else if (!this.data.f_pencarian) {
          return { code: 500, message: 'Pencarian Data ini harus diisi' }
      } else {
          return { code: 200, message: '' }
      }
    },
    async onSave() {
        const validation = this.validation()
        if (validation.code === 200) {
        var res = null
        const action = this.$route.params.action;
		const target = { f_keterangan: this.data.f_keterangan, f_pencarian: this.data.f_pencarian };
        if (action === 'tambah') {
            res = await axios.post(this.api+"/sertifikatsetting", target, Utils.authHeader());
        } else {
            res = await axios.put(this.api+"/sertifikatsetting/"+ this.$route.params.id, target, Utils.authHeader());
        }
        if (res.data.Error_code === 0) {
			this.alert = true
			this.message = res.data.Error_desc
            document.location = '/sertifikatsettingaction/detail/' + res.data.Data[0].f_sertifikat_setting_id;
            this.Getdata();
            this.aksiEdit = !this.aksiEdit
            this.aksiSave = false
        }  else {
			this.alert = true
			this.message = res.data.Error_desc
        }
        } else {
			this.alert = true
			this.message = validation.message
        }
    },
  },
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									
									<b-alert
											v-model="alert"
											variant="success"
											dismissible
											>{{ message }}</b-alert
										>

									<br>&nbsp;&nbsp;
									<br>
									<div class="table-responsive">
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Keterangan"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_keterangan"
										type="text"
										required
										placeholder="Keterangan"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Pencarian"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="data.f_pencarian"
										type="text"
										required
										placeholder="Pencarian"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label-for="input-horizontal"
									>
									<b-button variant="success" @click="onSave(data.f_sertifikat_setting_id)" v-if="aksiEdit">Simpan Data</b-button>
									&nbsp;
									<b-button variant="danger" @click="openEdit(data.f_sertifikat_setting_id)" v-if="!aksiEdit">Edit Data</b-button>
									</b-form-group>									

									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
